import './App.css';
import firebase from "firebase/app";
import "firebase/database";
import React from 'react'
import {db} from './firebaseConfig';

function App() {
  const test = db.ref().child('/object');

  let testValue;
  test.on('value', snap => testValue = snap.val());

  console.log('testValue', testValue);

  return (
    <span>{testValue}</span>
  );
}

export default App;
